@import '../../assets/scss/mixins.scss';
@import '../../assets/scss/variables.scss';
.edit {
  background-color: $dark-blue;
  @include flex(center, center);
  &__container {
    @include flex(center, center);
    flex-direction: row;
    background-color: $light-blue;
    width: 100vh;
    border-radius: 0.5rem;
    padding: 3rem 1rem;
  }

  &__image {
    @include flex(center, center);
    padding-right: 5rem;
    > img {
      @include circle(10rem, 10rem);
      border: 0.3rem solid black;
    }
  }

  &__form {
    @include flex(center, center);
    flex-direction: column;
    &-item {
      @include flex(flex-start, center);
      flex-direction: column;
      width: 100%;
      padding: 0 0 0.3rem 0;

      &-label {
        padding: 0.5rem 0;
        background-color: transparent;
        color: white;
        font-size: $font-size--xl;
        font-weight: bold;
      }

      &-input {
        width: 40vh;
        padding: 0.5rem;
        margin-top: 0.5rem;
        border: none;
        border: $medium-key-line black;
        background-color: transparent;
        color: white;
        font-size: $base-font-size;
        border-radius: 0.3rem;
        &:focus {
          outline: none;
        }

        &:read-only {
          color: $green-primary;
        }
        &::placeholder {
          color: rgba(0, 0, 0, 0.4);
          font-style: italic;
        }
      }

      &--error {
        padding-top: 0.3rem;
        background-color: transparent;
        color: $error-color;
        font-size: $font-size--xl;
      }
    }
    &-button {
      width: 100%;
      padding: 0.625rem 0;
      margin-top: 1rem;
      border-radius: 0.3rem;
      border: none;
      color: white;
      font-size: $font-size--xl;
      font-weight: $font-weight--semibold;
      background-color: $dark-blue;

      &-icon {
        stroke: $white-primary;
        width: 1.5rem;
        padding-right: 0.5rem;
      }
      &:hover {
        cursor: pointer;
        background-color: $violet-dark;
        color: white;
      }
    }
    &-option {
      padding: 1rem 0;
      > span:first-child {
        padding-right: 0.5rem;
        font-style: italic;
        font-size: $font-size--xl;
        color: $gray-dark;
      }
      &-link {
        text-decoration: none;
        font-size: $font-size--xl;
        font-weight: $font-weight--bold;
        color: black;

        &:hover {
          cursor: pointer;
          color: $green-secondary;
        }
      }
    }
    &-submit {
      &-result {
        @include flex(center, flex-start);
        width: 100%;
        padding: 1rem 0 0 0;
        &-status {
          &--error {
            color: red;
          }

          &--success {
            color: $success-color;
          }
        }

        &-message {
          &--success {
            font-size: smaller;
            padding-left: 0.5rem;
            color: $success-color;
          }
          &--error {
            font-size: smaller;
            padding-left: 0.5rem;
            color: $error-color;
          }
        }
      }
    }
  }
}
